import React from "react";
import moment from "moment";
import { Image, Divider } from "semantic-ui-react";

export default (props) => {
  const calculate = () => {
    const hour = parseFloat(moment(new Date()).format("HH"));
    if (hour <= 11 && hour >= 6) {
      return "Good Morning ";
    }
    if (hour >= 16 && hour <= 20) {
      return "Good Evening ";
    }
    if (hour >= 12 && hour <= 15) {
      return "Good Afternoon ";
    }
    return "Soo Jao";
  };
  return (
    <div>
      <Image
        src={require("../../images/complainify.png")}
        size="tiny"
        style={{
          transition: "all 1s",
        }}
        centered
      ></Image>
      {props.h2 ? (
        <div>
          <h2
            style={{
              color: "grey",
              textAlign: "center",
              fontFamily: "poppins",
            }}
          >
            {!props.username ? props.heading : props.username}
          </h2>
        </div>
      ) : (
        <div>
          <h1
            style={{
              color: "grey",
              textAlign: "center",
              fontSize: 16,
              fontFamily: "roboto",
              transition: "all 1s",
              fontWeight: "bold",
              fontWeight: "normal",
            }}
          >
            {!props.username
              ? props.heading
              : `${calculate()}, ` +
                props.username[0].toUpperCase() +
                props.username.slice(1, 8).toLowerCase() +
                "..."}
          </h1>
        </div>
      )}

      {props.divider ? <Divider></Divider> : ""}
    </div>
  );
};
