import React from "react";
// import { Image } from "semantic-ui-react";
import "./style.css";
import { CircularProgress } from "@material-ui/core";

export const T = (props) => {
  return (
    <div
      style={{
        height: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <CircularProgress
        style={{ width: 20, height: 20, color: "black" }}
      ></CircularProgress>
    </div>
  );
};
