import React, { Fragment } from "react";
import path from "path";
import "./style.css";
import Particles from "react-particles-js";
import { Responsive, Divider } from "semantic-ui-react";
const DP = (props) => {
  const ImageArray = [
    {
      src: "HomePageLook.png",
      desc: {
        head: "Got Complaints That You Can't Talk About...??",
        subHead: `Here You Can Post Complaints Anonymously...!!`,
      },
    },
    {
      src: "ChatView.png",
      desc: {
        head: "Want to Have Conversation with your College Mate...??",
        subHead: `Here you can do that as well...!!`,
      },
    },
    {
      src: "NotesView.png",
      desc: {
        head: "Find it hard to Search for Notes..??",
        subHead: `Searching Notes have Never been Easier...`,
      },
    },
    {
      src: "NotesView1.png",
      desc: {
        head: "Help Your Friends By Uploading Notes...!!",
        subHead: `Sharing is Caring...`,
      },
    },
  ];

  return (
    <Fragment>
      <div className="MyNavBar" style={{ background: "white", zIndex: 100 }}>
        <MyLink
          style={{ marginLeft: "auto" }}
          onClick={() => {
            props.history.push("/login");
          }}
        >
          Login
        </MyLink>
        <MyLink
          style={{ marginLeft: "auto" }}
          onClick={() => {
            props.history.push("/signup");
          }}
        >
          SignUp
        </MyLink>
      </div>
      <div
        style={{
          height: 400,
          width: "100%",
          padding: 20,
          background: "white",
          boxShadow: "0px 10px 10px rgba(0,0,0,0.1)",
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <img
          src={require("../../images/complainify.png")}
          className="rotateImage"
          style={{ width: 100, height: 100 }}
        ></img>
        <div
          style={{
            fontSize: "1.4rem",
            fontFamily: "poppins",
            cursor: "pointer",
            height: 60,
          }}
          onClick={() => {
            props.history.push("/signup");
          }}
          className="MyLink-Top"
        >
          Join Anonymity Today
        </div>
        <Divider horizontal>Or</Divider>
        <div
          style={{
            fontSize: "1.2rem",
            fontFamily: "poppins",
            cursor: "pointer",
            padding: 10,
            height: 60,
          }}
          className="MyLink-Top"
          onClick={() => {
            props.history.push("/login");
          }}
        >
          Already A Member..??
        </div>
      </div>
      <Responsive {...Responsive.onlyComputer}>
        <Particles
          params={{
            particles: {
              number: {
                value: 40,
              },
              size: { value: 10 },

              color: "#000000",
            },
          }}
          style={{
            position: "fixed",
            top: 0,
            backgroundColor: "rgba(0,0,0,0)",
            zIndex: -1,
          }}
        ></Particles>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        <Particles
          params={{
            particles: {
              number: {
                value: 40,
              },
              size: { value: 2 },

              color: "#000000",
            },
          }}
          style={{
            position: "fixed",
            top: 0,
            backgroundColor: "rgba(0,0,0,0)",
            zIndex: -1,
          }}
        ></Particles>
      </Responsive>
      <Responsive {...Responsive.onlyTablet}>
        <Particles
          params={{
            particles: {
              number: {
                value: 40,
              },
              size: { value: 6 },

              color: "#000000",
            },
          }}
          style={{
            position: "fixed",
            top: 0,
            backgroundColor: "rgba(0,0,0,0)",
            zIndex: -1,
          }}
        ></Particles>
      </Responsive>
      <Responsive {...Responsive.onlyComputer}>
        {ImageArray.map((ele, index) => {
          console.log(path.resolve(__dirname, ele.src));
          return (
            <div
              style={{
                display: "flex",
                width: "100%",
                flexDirection: index % 2 !== 0 ? "row-reverse" : "row",
              }}
            >
              <img
                src={require(`../../${ele.src}`)}
                className={
                  index % 2 == 0 ? "comingFromLeft" : "comingFromRight"
                }
                style={{
                  width: "45%",
                  boxShadow: "0px 0px 10px rgba(0, 0, 0, 0.2)",
                  margin: "30px 10px",
                  animationDelay: `${index * 0.2}s`,
                }}
              ></img>
              <div style={{ margin: "30px 10px" }}>
                <div
                  style={{
                    fontSize: "1.3rem",
                    fontWeight: "bold",
                    fontFamily: "poppins",
                  }}
                >
                  {ele.desc.head}
                </div>
                <div style={{ fontSize: "1rem" }}>{ele.desc.subHead}</div>
              </div>
            </div>
          );
        })}
      </Responsive>
      <Responsive {...Responsive.onlyTablet}>
        {ImageArray.map((ele, index) => {
          console.log(path.resolve(__dirname, ele.src));
          return (
            <div
              style={{
                display: "flex",
                width: "100%",
                flexDirection: index % 2 !== 0 ? "row-reverse" : "row",
              }}
            >
              <img
                src={require(`../../${ele.src}`)}
                className={
                  index % 2 == 0 ? "comingFromLeft" : "comingFromRight"
                }
                style={{
                  width: "45%",
                  boxShadow: "0px 0px 10px rgba(0, 0, 0, 0.2)",
                  margin: "30px 10px 10px 10px",
                  animationDelay: `${index * 0.2}s`,
                }}
              ></img>
              <div style={{ margin: "30px 10px" }}>
                <div
                  style={{
                    fontSize: "1.3rem",
                    fontWeight: "bold",
                    fontFamily: "poppins",
                  }}
                >
                  {ele.desc.head}
                </div>
                <div style={{ fontSize: "1rem" }}>{ele.desc.subHead}</div>
              </div>
            </div>
          );
        })}
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        {ImageArray.map((ele, index) => {
          console.log(path.resolve(__dirname, ele.src));
          return (
            <Fragment>
              <div style={{ margin: "30px 10px" }}>
                <div
                  style={{
                    fontSize: "1.3rem",
                    fontWeight: "bold",
                    fontFamily: "poppins",
                  }}
                >
                  {ele.desc.head}
                </div>
                <div style={{ fontSize: "1rem" }}>{ele.desc.subHead}</div>
              </div>
              <div
                style={{
                  width: "100%",
                }}
              >
                <img
                  src={require(`../../${ele.src}`)}
                  className={
                    index % 2 == 0 ? "comingFromLeft" : "comingFromRight"
                  }
                  style={{
                    width: "100%",
                    boxShadow: "0px 0px 10px rgba(0, 0, 0, 0.2)",
                    marginBottom: 30,
                    marginTop: 10,
                    animationDelay: `${index * 0.2}s`,
                  }}
                ></img>
              </div>
            </Fragment>
          );
        })}
      </Responsive>
    </Fragment>
  );
};

const MyLink = ({ onClick, children }) => {
  return (
    <div
      className="MyLink-Top"
      onClick={() => {
        onClick();
      }}
    >
      <div
        style={{
          fontFamily: "poppins",
          fontSize: 14,
          fontWeight: "bold",
          textAlign: "center",
        }}
      >
        {children}
      </div>
    </div>
  );
};

export default DP;
