import React, { useState } from "react";
import {
  Paper,
  Grid,
  Fab,
  createMuiTheme,
  ThemeProvider,
  CircularProgress,
  Snackbar,
} from "@material-ui/core";
import { connect } from "react-redux";
import { Edit } from "@material-ui/icons";
import "./style.css";
import { blue, orange } from "@material-ui/core/colors";
import { Pagination } from "@material-ui/lab";
import { getMyProblems as getMyPosts } from "../../../../ApiRequests/request";
import RenderProblems from "../problemList/ReanderProblems";
import { Select } from "semantic-ui-react";
import PostProblems from "../../PostProblem/PostProblem";
import Selector from "../../../TypeSelector/index";
const theme = createMuiTheme({
  palette: {
    primary: {
      main: orange[500],
    },
    secondary: {
      main: blue[500],
    },
  },
});

const MyProblems = (props) => {
  const centered = { display: "flex", justifyContent: "center" };
  const [isFetching, setFetching] = useState(true);
  const [isError, setError] = useState(false);
  const [MyProblems, setProblems] = useState([]);
  const token = localStorage.getItem("AnonymityToken#123");
  const [isFetched, setFetched] = useState(false);
  const [offset, setOffset] = useState(10);
  const [totalProblems, setCount] = useState(0);
  const [page, setPage] = useState(1);
  const [start, setStart] = useState(0);
  const [sortBy, setSortBy] = useState("Latest");
  const [postsProblem, setPost] = useState(false);
  const [Type, setType] = useState("_");
  const [options1, setOptions1] = useState(3);
  const [options2, setOptions2] = useState(3);

  const fetchNewProblems = (token, start, offset, sortBy, Type) => {
    setProblems([]);
    getMyPosts(token, start, offset, sortBy, Type)
      .then(async (res) => {
        if (res.status === 200) {
          const { problems, count } = await res.json();

          setProblems([...problems]);
          setCount(count);
          setTimeout(() => setFetching(false), 0);
          return;
        }
        setError(true);
        setTimeout(() => setFetching(false), 0);
      })
      .catch((e) => {
        setError(true);
        setTimeout(() => setFetching(false), 0);
      });
    setFetched(true);
  };
  if (!isFetched) {
    fetchNewProblems(token, start, offset);
  }

  return (
    <ThemeProvider theme={theme}>
      <PostProblems
        open={postsProblem}
        onClose={() => {
          setPost(false);
        }}
        hasUpload={true}
        UserName={props.username}
        profilePhoto={props.profilePhoto}
      ></PostProblems>
      <Snackbar
        open={isError}
        onClose={() => {
          setError(false);
        }}
        autoHideDuration={6000}
        message="Error Loading New Problems"
      ></Snackbar>
      <Grid container style={{ marginTop: 10 }}>
        <Grid item xs={12}>
          <div>
            <div
              style={{
                display: "flex",
                overflow: "auto",
                justifyContent: "center",
              }}
              className="horizontalScroll"
            >
              <Selector
                title="Sort By"
                setClose={() => setOptions1(3)}
                value={sortBy}
                options={options1}
                onSelect={(e, { value }) => {
                  setSortBy(value);
                  setFetching(true);
                  setPage(1);
                  fetchNewProblems(token, 0, offset, value, Type);
                  setOptions1(3);
                }}
                onButtonClick={() => {
                  setOptions1(1);
                }}
              ></Selector>

              <Selector
                title="Type"
                value={Type}
                options={options2}
                setClose={() => setOptions2(3)}
                onButtonClick={() => setOptions2(2)}
                onSelect={(e, { value }) => {
                  setType(value);
                  setFetching(true);
                  setPage(1);
                  fetchNewProblems(token, 0, offset, sortBy, value);
                  setOptions2(3);
                }}
              ></Selector>
            </div>

            <div style={{ display: "flex", justifyContent: "center" }}>
              <RenderProblems
                isLoading={isFetching}
                problems={MyProblems}
                {...props}
              ></RenderProblems>
            </div>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                marginTop: 100,
                marginBottom: 100,
              }}
            >
              {isFetching ? (
                <div
                  style={{
                    display: "flex",
                    justifyContent: "center",
                    marginTop: 100,
                    marginBottom: 100,
                  }}
                >
                  <CircularProgress
                    style={{ width: 50, height: 50, color: "black" }}
                  ></CircularProgress>
                </div>
              ) : (
                <div
                  style={{
                    display: "flex",
                    justifyContent: "center",
                    marginTop: 50,
                    marginBottom: 50,
                  }}
                >
                  {totalProblems > 10 ? (
                    <Pagination
                      color="secondary"
                      page={page}
                      onChange={(event, page) => {
                        setFetching(true);
                        fetchNewProblems(
                          token,
                          (page - 1) * offset,
                          offset,
                          sortBy,
                          Type
                        );
                        window.scrollTo(0, 0);
                        setPage(page);
                      }}
                      count={parseInt(totalProblems / offset) + 1}
                    ></Pagination>
                  ) : (
                    ""
                  )}
                </div>
              )}
            </div>
          </div>
        </Grid>
      </Grid>
      <Fab
        color="secondary"
        style={{
          textTransform: "none",
          fontFamily: "poppins",
          color: "white",
          position: "fixed",
          left: "2%",
          bottom: "10%",
          zIndex: 100,
        }}
        onClick={() => {
          setPost(true);
        }}
      >
        <Edit></Edit>
      </Fab>
    </ThemeProvider>
  );
};

const mapStateToProps = (state) => {
  return {
    username: state.userReducer.username,
    profilePhoto: state.userReducer.profilePhoto,
  };
};

export default connect(mapStateToProps)(MyProblems);
