import React, { useState } from "react";
import {
  TextField,
  MenuItem,
  Button,
  Snackbar,
  Avatar,
} from "@material-ui/core";
import {
  Modal,
  Form,
  TextArea,
  Button as Button1,
  Image,
  Divider,
} from "semantic-ui-react";
import { PhotoCamera, Public, SecurityTwoTone } from "@material-ui/icons";
import { postMyPost, postPhoto } from "../../../ApiRequests/request";
import { Upload, message } from "antd";
import url from "../../../strings/url";
const token = localStorage.getItem("AnonymityToken#123");
const PostProblem = (props) => {
  const [isLoading, setLoading] = useState(false);
  const [status, setStatus] = useState("unknown");
  const [description, setDescription] = useState("");
  const [isUploaded, setUploaded] = useState(false);
  const [problemImageUrl, setUrl] = useState(null);
  const [isAnonymous, setAnonymous] = useState(false);
  const [problemType, setPT] = useState("Others");

  return (
    <div>
      {status === "error" ? (
        <Snackbar
          open={status !== "unknown"}
          message="Error Posting"
          autoHideDuration={3000}
          onClose={() => setStatus("unknown")}
        ></Snackbar>
      ) : (
        ""
      )}
      {status === "success" ? (
        <Snackbar
          open={status !== "unknown"}
          message="SuccessFul"
          autoHideDuration={3000}
          onClose={() => setStatus("unknown")}
        ></Snackbar>
      ) : (
        ""
      )}

      <Modal dimmer="inverted" open={props.open} onClose={props.onClose}>
        <Modal.Header>
          <div style={{ display: "flex", alignItems: "center" }}>
            <Avatar
              src={props.profilePhoto}
              style={{ marginRight: 10 }}
            ></Avatar>
            <div
              style={{
                fontWeight: "500",
                fontSize: 14,
                fontFamily: "poppins",
              }}
            >
              <div>
                <b>{props.UserName + " " || "Unknown" + " "}</b>
                Have a Problem
              </div>
            </div>
          </div>

          <Divider></Divider>
          <div style={{ display: "flex" }}>
            <TextField
              label="Listed As"
              select
              variant="filled"
              color="secondary"
              SelectProps={{
                style: { backgroundColor: "rgba(0,0,0,0.01)" },
              }}
              style={{ marginRight: 10 }}
              fullWidth
              value={isAnonymous}
              onChange={(a, b) => {
                setAnonymous(b.props.value);
              }}
              name="Type1"
            >
              <MenuItem key="Public" value={false}>
                Public
              </MenuItem>
              <MenuItem key="Anonymous" value={true}>
                Anonymous
              </MenuItem>
            </TextField>
            <TextField
              SelectProps={{
                style: { backgroundColor: "rgba(0,0,0,0.01)" },
              }}
              label="Category"
              variant="filled"
              value={problemType}
              select
              color="secondary"
              onChange={(a, b) => {
                setPT(b.props.value);
              }}
              name="Type2"
              fullWidth
            >
              <MenuItem key="Technical" value="Technical">
                Technical Issue
              </MenuItem>
              <MenuItem key="Academic" value="Academic">
                Academic Issue
              </MenuItem>
              <MenuItem key="Registration" value="Registration">
                Registration Issue
              </MenuItem>
              <MenuItem key="TP" value="TP">
                Training And Placement
              </MenuItem>
              <MenuItem key="Others" value="Others">
                Other
              </MenuItem>
            </TextField>
          </div>
        </Modal.Header>
        <Modal.Content>
          <Form loading={isLoading}>
            <TextArea
              style={{ height: 100, fontSize: 15, borderRadius: "0px" }}
              value={description}
              onChange={(e) => {
                setDescription(e.target.value);
              }}
              placeholder="Describe Your Problem"
            ></TextArea>
          </Form>
        </Modal.Content>
        {props.hasUpload ? (
          <Modal.Content>
            <Upload
              method="PUT"
              accept="image/*"
              headers={{
                Authorization: localStorage.getItem("AnonymityToken#123"),
                contentType: "application/json",
              }}
              name="image"
              action={`${url}/user/post/photo`}
              onChange={(info) => {
                if (info.file.status === "done") {
                  setUploaded(true);
                  setLoading(false);
                  setUrl(info.file.response);
                } else if (info.file.status === "removed") {
                  setLoading(false);
                  setUploaded(false);
                } else if (info.file.status === "uploading") {
                  setLoading(true);
                } else if (info.file.error) {
                  setLoading(false);
                }
              }}
              onRemove={() => {
                console.log("on Remove called");
                const headers = new Headers();
                headers.append("Authorization", token);
                headers.append("content-type", "application/json");
                const request = new Request(
                  `${url}/post/photo/${problemImageUrl.key}`,
                  { method: "DELETE", headers }
                );
                fetch(request)
                  .then((res) => {
                    console.log(res.status);
                  })
                  .catch((e) => {
                    console.log(e);
                  });
                setUrl(null);
              }}
              disabled={isUploaded}
            >
              <Button
                style={{
                  fontFamily: "poppins",
                  fontWeight: "bold",
                  textTransform: "none",
                }}
                disabled={isUploaded}
              >
                <div style={{ marginRight: 10 }}>Upload Photo</div>
                <PhotoCamera
                  style={{ color: "rgba(44,130,201,1)" }}
                ></PhotoCamera>
              </Button>
            </Upload>
          </Modal.Content>
        ) : (
          ""
        )}

        <Modal.Actions>
          <Button1
            color="blue"
            // inverted
            size="medium"
            style={{
              marginBottom: 10,
              // color: "white",
              marginRight: 10,
              textTransform: "none",
              fontFamily: "poppins",
            }}
            disabled={description.length < 1 || isLoading}
            onClick={() => {
              setLoading(true);
              const token = localStorage.getItem("AnonymityToken#123");
              postMyPost(
                token,
                description,
                "problem",
                problemImageUrl,
                isAnonymous,
                problemType
              )
                .then((res) => {
                  if (res.status === 200) {
                    window.location.reload(false);
                    setStatus("success");
                    props.onClose();
                    return;
                  }
                  setLoading(false);
                  setStatus("error");
                  props.onClose();
                })
                .catch((err) => {
                  setLoading(false);
                  setStatus("error");
                });

              setUrl(null);
            }}
            variant="outlined"
          >
            Post Problem
          </Button1>
          <Button1
            size="medium"
            style={{
              marginBottom: 10,
              textTransform: "none",
              fontFamily: "poppins",
            }}
            onClick={() => {
              setLoading(false);
              props.onClose();
              if (problemImageUrl) {
                const headers = new Headers();
                headers.append("Authorization", token);
                headers.append("content-type", "application/json");
                const request = new Request(
                  `${url}/post/photo/${problemImageUrl.key}`,
                  { method: "DELETE", headers }
                );
                fetch(request)
                  .then((res) => {
                    console.log(res.status);
                    setUrl(null);
                  })
                  .catch((e) => {
                    setUrl(null);
                    console.log(e);
                  });
              }
              setUploaded(false);
            }}
            // color="red"
            // inverted
          >
            Close
          </Button1>
        </Modal.Actions>
      </Modal>
    </div>
  );
};

export default PostProblem;
