import React, { useState } from "react";
import { Checkbox } from "@material-ui/core";
import "./style.css";

const Selector = ({
  value,
  title,
  options,
  onSelect,
  onButtonClick,
  setClose,
}) => {
  const sortByOptions = [
    { key: "Latest", value: "Latest", text: "Latest" },
    // { key: "UpVote", value: "UpVote", text: "Up Votes" },
    // { key: "DownVote", value: "DownVote", text: "Down Votes" },
    { key: "oldest", value: "oldest", text: "Oldest" },
  ];
  const typeOptions = [
    { key: "Technical", value: "Technical", text: "Technical" },
    { key: "Academic", value: "Academic", text: "Academic" },
    { key: "Registration", value: "Registration", text: "Registration" },
    { key: "Training", value: "TP", text: "Placements" },
    { key: "Others", value: "Others", text: "Others" },
    { key: "All", value: "_", text: "All" },
  ];
  return (
    <div style={{ margin: 10 }}>
      {options === 1 ? (
        <div
          onClick={() => setClose()}
          style={{
            backgroundColor: "rgba(0,0,0,0.1)",
            zIndex: 1000,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            position: "fixed",
            top: 0,
            width: "100vw",
            height: "100vh",
            left: 0,
          }}
        >
          <div
            className="Zoom1"
            style={{
              filter: "none",
              color: "black",
              background: "rgba(255,255,255,1)",
              padding: 10,
              borderRadius: 6,
              boxShadow: "0px 0px 10px rgba(255,255,255,0.3)",
              border: "none",
              overflow: "auto",
              maxHeight: "300px",
              zIndex: 1001,
            }}
          >
            {sortByOptions.map((option) => {
              return (
                <div
                  key={option.key}
                  style={{
                    width: 250,
                    height: 50,
                    position: "relative",
                    cursor: "pointer",
                  }}
                  className="centerThis"
                  onClick={() => {
                    onSelect("hello World", { value: option.value });
                  }}
                >
                  <Checkbox
                    style={{
                      position: "absolute",
                      left: 0,
                    }}
                    checked={value === option.value}
                  ></Checkbox>
                  <div
                    style={{
                      fontFamily: "poppins",
                      fontWeight: "bold",
                      fontSize: 14,
                      color: "grey",
                    }}
                  >
                    {option.text}
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      ) : (
        ""
      )}
      {options === 2 ? (
        <div
          onClick={() => setClose()}
          style={{
            backgroundColor: "rgba(0,0,0,0.1)",
            zIndex: 1000,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            position: "fixed",
            top: 0,
            width: "100vw",
            height: "100vh",
            left: 0,
          }}
        >
          <div
            className="Zoom1"
            style={{
              filter: "none",
              color: "black",
              background: "rgba(255,255,255,1)",
              padding: 10,
              borderRadius: 6,
              boxShadow: "0px 0px 10px rgba(255,255,255,0.3)",
              border: "none",
              overflow: "auto",
              maxHeight: "300px",
              zIndex: 1001,
            }}
          >
            {typeOptions.map((option) => {
              return (
                <div
                  key={option.key}
                  style={{
                    width: 250,
                    height: 50,
                    position: "relative",
                    cursor: "pointer",
                  }}
                  className="centerThis"
                  onClick={() => {
                    onSelect("hello World", { value: option.value });
                  }}
                >
                  <Checkbox
                    style={{
                      position: "absolute",
                      left: 0,
                    }}
                    checked={value === option.value}
                  ></Checkbox>
                  <div
                    style={{
                      fontFamily: "poppins",
                      fontWeight: "bold",
                      fontSize: 14,
                      color: "grey",
                    }}
                  >
                    {option.text}
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      ) : (
        ""
      )}
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
        }}
      >
        <button
          className="selectorButton"
          onClick={() => {
            onButtonClick();
          }}
        >
          <div style={{ fontSize: 12, fontFamily: "poppins" }}>{title}</div>
          {value !== "_" ? value : "All"}
        </button>
      </div>
    </div>
  );
};

export default Selector;
